.page-home {
  min-height: 1048px;
  height: 50vh;
  min-width: var(--base-min-width);
  width: 100vw; }
  .page-home__background {
    width: 100%;
    height: 100%;
    background-image: url(images/1e3643_s_4_2.jpg);
    background-color: #212121;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    overflow: hidden;
    opacity: 0.9; }
  .page-home__content {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center; }

.home-content {
  position: relative; }
  .home-content__header {
    margin: 375px 0 27px 0; }
  .home-content__second-header {
    margin-bottom: 32px; }

.header-description {
  font-family: var(--font-family-header-pages);
  font-size: 119px;
  font-weight: 900;
  line-height: 1em;
  text-align: center; }
  .header-description__text {
    color: white;
    text-transform: uppercase;
    letter-spacing: 0.06em; }

.second-header-description {
  font-family: sans-serif;
  font-size: 19px;
  font-weight: 100;
  line-height: 1.4em;
  text-align: center; }
  .second-header-description__text {
    color: white;
    text-transform: uppercase;
    letter-spacing: 0.45em; }
