.page-footer {
  background: rgba(0, 0, 0, 0.9);
  min-width: var(--base-min-width); }
  .page-footer__content {
    height: var(--size-footer-content);
    display: flex;
    justify-content: space-between;
    align-items: center; }

.footer-nav {
  color: white;
  display: flex;
  justify-content: space-between; }

.footer-copyright {
  font-family: NavMenuFonts;
  font-weight: 200;
  font-size: 14px;
  color: var(--copyright-color);
  letter-spacing: 0.1em; }
  .footer-copyright__link {
    text-decoration: none;
    font-family: NavMenuFonts;
    color: var(--copyright-color); }

.footer-logo {
  position: relative;
  height: 26px;
  width: 26px;
  margin-right: 15px; }
  .footer-logo__link {
    display: block;
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%; }
    .footer-logo__link_fb {
      display: block;
      width: 100%;
      height: 100%;
      background-size: cover; }
    .footer-logo__link_twit {
      display: block;
      width: 100%;
      height: 100%;
      background-size: cover; }
    .footer-logo__link_insta {
      display: block;
      width: 100%;
      height: 100%;
      background-size: cover; }
    .footer-logo__link:last-child {
      margin-right: 0px; }
