.page-membership {
  background: #fafafa;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 544px;
  min-width: var(--base-min-width); }
  .page-membership__content {
    margin: 100px 0 23px;
    color: #000; }
  .page-membership__descr {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 980px; }

.membership-content {
  color: #000;
  display: flex;
  justify-content: center;
  align-items: center; }
  .membership-content__container {
    width: 100%; }
  .membership-content__h1 {
    font-family: var(--font-family-header-pages), serif;
    letter-spacing: 0.06em;
    font-size: var(--font-size-header-pages);
    text-transform: uppercase; }
  .membership-content__p {
    font-family: NavMenuFonts;
    font-size: 15px;
    text-align: center;
    margin: 0 0 36px; }
  .membership-content__descr {
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: nowrap;
    flex: none;
    width: calc(33.33% - 10px);
    margin-top: 14px; }
  .membership-content__text {
    font-size: 15px;
    font-family: NavMenuFonts;
    padding-left: 15px; }
  .membership-content__logo {
    width: 56px; }
    .membership-content__logo > svg {
      width: 100%;
      display: block; }

.second-membership {
  margin-top: 60px; }
  .second-membership__logo {
    width: 56px;
    transform: translate(30%, -30%); }
    .second-membership__logo > svg {
      width: 60%;
      display: block;
      padding-top: 14px; }
